import { useCallback } from '@wordpress/element';

/**
 * –•—É–∫ –¥–ª—è –≤—ã–±–æ—Ä–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –≤—Å–µ—Ö –∞—Ç—Ä–∏–±—É—Ç–æ–≤,
 * –≤–∫–ª—é—á–∞—è adaptive/responsive –≤–µ—Ä—Å–∏–∏, —Å WebP.
 */
export const useOptimizedMedia = (setAttributes) => {
  const onSelectImage = useCallback((media) => {
    if (!media || !media.id) return;

    // –ë–µ—Ä—ë–º responsive –∏–∑ REST API, –∫–æ—Ç–æ—Ä—ã–π –≤–µ—Ä–Ω—É–ª PHP
    const responsive = media.responsive || {
      webp: '',
      jpg: '',
      default: media.url || '',
    };

    setAttributes({
      imageUrl: media.url || '',
      imageId: media.id,
      imageAlt: media.alt || '',
      responsive: responsive,
    });

  }, [setAttributes]);

  return { onSelectImage };
};
